body,
* {
    border: 0;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-size: 16px;
    font-family: sans-serif;
}

html, 
body {
    width: 100%;
    min-height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: whitesmoke;
}

/* Style basic site containers */

.header-container {
    background-color: rgb(21, 21, 63);
    color: white;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    padding: 1rem;
}

.main-container {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: whitesmoke;
}

.content-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: min(100%, 65rem);
    padding: 1rem 4rem;
}

/* Basic header section styling */
.header-container * {
    max-width: 100%;
}

.header-container .logo {
    height: 4.5rem;
    width: 4.5rem;
    padding: .1rem;
    background-color: white;
    border-radius: 6px;
}

.searchbar {
    display: flex;
    align-items: center;
}

.search-button {
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
}

.search-button img {
    background-color: rgb(21, 21, 63);
    width: 2rem;
    height: 2rem;
}

#search {
    font-size: 1.2rem;
    padding: .5rem 1rem;
    color: black;
    border-radius: 6px;
    background-color: whitesmoke;
    border: 2px solid rgb(21, 21, 63);
    outline: none;
}

#search:hover,
#search:focus {
    outline: none;
    border: 2px solid rgb(69, 69, 70);
    background-color: rgb(198, 198, 230);
}

#search::placeholder {
    color:rgb(64, 64, 78);
}

.degrees-button {
    width: 4.5rem;
    height: 4.5rem;
    padding: 1rem;
    background-color: white;
    color: rgb(34, 34, 65);
    font-size: 1.8rem;
    border-radius: 6px;
}

.degrees-button:hover {
    background-color: lightgray;
    cursor: pointer;
}
/* Overview section basic styling */

.overview {
    width: 100%;
    border-radius: 6px;
    background-color: gray;
    height: min-content;
    color: white;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.overview-header {
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 6px 6px 0px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.overview .location {
    font-size: 1.25rem;
    font-weight: bold;
}

.overview .time {
    font-size: 1.1rem;
}

.overview-info {
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.overview-info .temperature {
    font-size: 5rem;
    font-weight: bold;
    padding-top: .25rem;
}

.overview-info .weather,
.overview-info .day-night {
    font-size: 1.4rem;
    font-weight: bold;
    padding-top: .25rem;
}

/* Dynamic styling for the overview background based on current conditions */
.overview.default {
    background-color: gray;
}

.overview.thunderstorm {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.overview.snow {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.overview.rain {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.overview.fog {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

.overview.cloudy {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.overview.clear {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

/* Today's info basic styling */
.today {
    width: 100%;
    background-color: white;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    height: min-content;
}

.today-header {
    padding: 1.2rem;
    font-weight: bold;
}

.today-header p {
    font-size: 1.2rem;
}

.today .basic-info {
    display: flex;
    padding: 1rem;
    justify-content: space-between;
}

.feels-like * {
    font-weight: bold;
    margin-left: 1rem;
}

.feels-like .temp {
    font-size: 3rem;
}

.basic-info img {
    width: 1.5rem;
    height: 1.5rem;
}

.sunrise-sunset {
    display: flex;
}

.sunrise-sunset div {
    display: flex;
    align-items: center;
    margin-left: .5rem;
}

.today .detailed-info {
    display: grid;
    padding: 1rem;
    gap: 0rem 1rem;
    grid-template-columns: 1fr 1fr;
}

.today .detailed-info > * {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid rgb(196, 196, 196);
    padding: .75rem 1rem;
    align-items: center;
    height: min-content;
}

.today .detailed-info .info-stuff {
    display: flex;
    align-items: center;
}

.info-stuff img {
    width: 1.5rem;
    height: 1.5rem;
    object-fit: contain;
    margin-right: .5rem;
}

/* Hourly section basic styling */

.hourly {
    display: flex;
    width: 100%;
    flex-direction: column;
    background-color: white;
    border-radius: 6px;
}

.hourly-header {
    padding: 1rem 1rem 1rem 1rem;
    background-color: rgb(34, 34, 65);
    border-radius: 6px 6px 0px 0px;
}

.hourly-header p {
    font-size: 1.2rem;
    color: white;
}

.forecast-container-container {
    display: flex;
    padding: .5rem;
}

.forecast-container {
    display: flex;
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    height: min-content;
    gap: 1.5rem;
    padding: .5rem 1rem 1rem;
    background-color: white;
}

.hour {
    text-wrap: nowrap;
    width: min-content;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.hour .icon {
    width: 2rem;
    height: 2rem;
}

.rainfall {
    display: flex;
}

.rainfall p {
    font-size: .8rem;
}

.rainfall-img {
    height: .8rem;
    width: .8rem;
}

/* Style the 10-day forecast section */

.ten-day {
    display: flex;
    flex-direction: column;
    width: 50%;
    background-color: white;
    border-radius: 6px;
}

.ten-day-header {
    background-color: rgb(34, 34, 65);
    color: white;
    padding: 1rem;
    border-radius: 6px 6px 0px 0px;
}

.ten-day-header p {
    font-size: 1.2rem;
}

.ten-day-forecast-container {
    padding: .5rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.day { 
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: .5rem 1rem;
    border-bottom: 1px solid gray;
}

.day[data-index="9"] {
    border-bottom: none;
}

.day .date {
    width: 7rem;
    font-size: 1rem;
}

.day .info {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    align-items: center;
}

.day .condition {
    height: 2rem;
    width: 2rem;
}

.day .max-min {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
